@import url(https://fonts.googleapis.com/css2?family=Baloo+Tammudu+2&display=swap);
* {
  scroll-behavior: smooth;
}
* {
  scroll-behavior: smooth;
  margin: 0;
  padding: 0;
}

.App{
  margin-top: 0;
  padding-top: 0;
  min-height: 100vh;
  background-color: rgb(96, 104, 103);
  color: rgb(221, 77, 24);
  padding-bottom: 100px;
  padding-top: 80px;
}

@media(max-width:700px){
  .App{
    padding-top: 70px;
  }
}

.pagetitle{
  text-align: center;
  width: 30%;
  height: auto;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-transform: uppercase;
  -webkit-align-items: center;
          align-items: center;
  font-family: 'Do Hyeon', sans-serif;
  font-size: 2vw;
  padding: 4px;
  border-radius: 50px;
  color: #F05454;
  box-shadow: 2px 5px 10px black;
  background-color: rgb(51, 51, 51);
}


@media screen and (max-width: 700px){
  .pagetitle{
      padding-top: 0;
  }
}

.header{
    width: 100%;
    cursor: pointer;
    position: fixed;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    text-transform: uppercase;
    background-color: rgb(51, 51, 51);
    font-family: 'Do Hyeon', sans-serif;
    font-size: 3vw;
    padding-bottom: 2px;
    padding-top: 0;
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 0;
    box-shadow: 1px 2px 5px black;
    color: #39A2DB;
    z-index: 100;
}

@media screen and (max-width: 700ps){
    .header{
        padding-bottom: 0;
    }
}
.media{
    color: rgb(253, 253, 253);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 220px;
    height: 437px;
    padding: 5px;
    margin: 5px;
    background-color: rgb(39, 39, 39);
    border: 0;
    border-radius: 5px;
    position: relative;
    font-family: 'Do Hyeon', sans-serif;
    cursor: pointer;
    transition: 0.4s;
}

.media:hover{
    background-color: #dddde0;
    color: rgb(50, 51, 50);
    box-shadow: 6px 12px 13px rgb(44, 44, 44);
}

@media(max-width:550px){
    .media{
        width: 46%;
    }
}

.poster{
    border: 0;
    border-radius: 5px;
}

.title{
    width:100%;
    text-align: center;
    font-size: 17px;
    padding: 8px 0;
}

.subtitle{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding-bottom: 3px;
}
.content-landscape{
    object-fit: contain;
    border-radius: 10px;
}

.content-portrait{
    display: none;
    object-fit: contain;
    border-radius: 10px;
}

.tagline{
    padding-bottom: 10px;
    -webkit-align-self: center;
            align-self: center;
    font-family: 'Do Hyeon', sans-serif;
    color: white;
}

.contentmodal{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    height: 100%;
    width: auto;
    overflow-y: scroll;
    scrollbar-width: none;
    font-family: 'Do Hyeon', sans-serif;
}

.contentmodal::-webkit-scrollbar{
    display: none;
}

.content-about{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    background-color: rgb(39, 39, 39);
    border-radius: 10px;
    box-shadow: 2px 5px 10px black;
}

.content-title{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: #dddde0;
    margin-left: 17px;
    margin-right: 17px;
    border-radius: 5px;
    color: rgb(39, 39, 39);
    padding: 5px;
}

.content-des{
    display: -webkit-flex;
    display: flex;
    height: auto;
    overflow-y: scroll;
    padding: 15px;
    border-radius: 5px;
    margin-left: 17px;
    margin-right: 17px;
    scrollbar-width: thin;
    box-shadow: inset 0 0 5px black;
    text-align: justify;
    background-color: #dddde0;
    color: rgb(39, 39, 39);
}

.content-des::-webkit-scrollbar{
    display: none;
}

@media (min-width: 835px){
    .content-landscape{
        display: none;
    }
    .content-portrait{
        display: -webkit-flex;
        display: flex;
        width: 38%;
    }
    .contentmodal{
        -webkit-flex-direction: row;
                flex-direction: row;
        -webkit-justify-content: space-around;
                justify-content: space-around;
        padding: 10px 0;
    }
    .content-about{
        width: 58%;
        padding: 0;
        height: 100%;
    }
    .content-title{
        font-size: 3.5vw;
    }
    .content-des{
        font-size: 22px;
    }
}

.rating{
    font-weight: bolder;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    background-color: rgb(39, 39, 39);
    width: 10%;
    border-radius: 10px;
    padding-bottom: 0;
    margin-bottom: 0;
    -webkit-align-self: center;
            align-self: center;
}

.genres{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}

.modal-btn:hover{
    box-shadow: 2px 5px 10px black;
}
.trending{
    padding-top: 20px;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    grid-gap: 10px;
    -webkit-justify-content: center;
            justify-content: center;
}

.typeof{
    color: black;
    font-weight: bolder;
    font-family:'Times New Roman', Times, serif;
    font-style:oblique;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
}
.movie{
    padding-top: 2px;
    display: grid;
    grid-template-columns: auto auto auto auto auto;
    grid-template-rows: auto auto auto auto;
    grid-gap: 10px;
    -webkit-justify-content: center;
            justify-content: center;
}



